.accordion__title{
    padding-left: 0px;
    width: 100%;
    height: 100%;
    display: flex;
    align-items : center;
}

.title_text{
    display: flex;
    align-items : end;
    justify-content: start;
    font-weight: 700;
}

.title_subtext{
    display: flex;
    align-items : center;
    font-weight: 500;
    margin-left:8px;
    color:var(--main-gray);
}

.accordion{
    border-radius: var(--br-01);
    box-sizing: border-box;
    box-shadow: var(--default-shadow-01);
    width:95%;
    box-sizing: border-box;
    /* 전체 온오프 패딩 조절 */
    padding:10px 15px;
    transition: all 0.3s ease-in;
}

.dis-accordion{
    border-radius: var(--br-01);
    box-sizing: border-box;
    box-shadow: var(--default-shadow-01);
    width:95%;
    box-sizing: border-box;
    /* 전체 온오프 패딩 조절 */
    padding:10px 15px;
    background-color: var(--main-light-gray);
}

.accordion__header{
    display: flex;
    flex-direction: column;
    cursor:pointer;
    box-sizing: border-box;
}

.accordion__header:hover{
    color:var(--main-color);
}
.dis-accordion__header{
    display: flex;
    flex-direction: column;
    cursor:pointer;
    box-sizing: border-box;
}


#panel1a-header:hover{
    color:var(--main-color);
}

.accordion__body{
    display: flex;
    flex-direction: column;
    justify-content :center;
    align-items :center;
    box-sizing: border-box;
    width:100%;
    color:#757E8A;
}